.video {
  background-color: $grey-dark-bg;
  padding: 100px 0 426px;
  margin: 0 0 213px;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (max-width: 1023px) {
    padding: 100px 0 231px;
    margin: 0 0 140px;
  }

  @media (max-width: 767px) {
    padding: 100px 0 200px;
    margin: 0 0 140px;
  }
}

.video__wrapper {
  width: 100%;
  padding: 0 17%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;

  @media (max-width: 767px) {
    padding: 0 6%;
  }
}

.video__container {
  position: relative;
  text-align: center;
  position: absolute;
  top: 138%;

  @media (max-width: 767px) {
    padding: 0 6%;
    width: 100%;
  }

  img {
    background-color: $black-input;
  }
}

.video__image {

  @media (max-width: 1023px) {
    max-width: 503px;
  }

  @media (max-width: 767px) {
    width: 100%;
  }
}

.video__title {
  font-size: 30px;
  line-height: 30px;
  color: $white;
  @include opensans-semibold;
  margin: 0 0 20px;
  padding: 0;
  text-align: center;

  @media (max-width: 1023px) {
    font-size: 24px;
    line-height: 30px;
  }
}

.video__description {
  font-size: 16px;
  line-height: 28px;
  color: $white;
  opacity: 0.7;
  @include opensans-light;
  margin: 0;
  padding: 0 25%;
  text-align: center;

  @media (max-width: 1023px) {
    font-size: 16px;
    line-height: 28px;
    padding: 0 15%;
  }

  @media (max-width: 767px) {
    padding: 0;
  }
}


.video__button {
  position: absolute;
  top: 44%;
  left: 47%;
  z-index: 3;
}
